@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');


* {
    box-sizing: border-box;
    font-family: 'poppins', sans-serif;
    // outline: 1px solid blue;
}

input,
label {
    border: #707070 1px solid;
    outline: none;
    height: 50px;
    width: 320px;
    padding-inline: 15px;
    border-radius: 15px;
    font-size: 15px;
}

i{
    color: #20404A;
}

button,
input[type="file"],
a,
label {
    cursor: pointer;
}

input:focus:valid {
    border-color: green;
    border-width: 2px;
}

input:invalid {
    border-color: red;
    border-width: 2px;
    animation: shake .2s;
}


body {
    // background: #e6e6e6;
    padding: 0;
    margin: 0;
    font-family: "segoe ui";

    .navbar {
        width: 100%;
        position: fixed;
        top: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 70px;
        padding: 20px 50px;
        color: #fff;
        font-size: 16px;
        font-weight: 500;

        .logo {
            height: 100%;
        }

        .navs {
            display: flex;
            list-style-type: none;
            gap: 50px;


        }

        .signin {
            display: flex;
            gap: 20px;

        }


    }

    .banner {
        height: 593px;
        background-image: url(images/background.png);
        background-position: top;
        background-size: contain;
        background-repeat: no-repeat;

        // overflow: hidden;
        .padding {
            height: 70px;
            width: 100%;
        }

        .contents {
            display: flex;
            align-items: center;
            justify-content: space-around;
            height: calc(100% - 70px);

            .left {
                width: 50%;
                display: flex;
                // align-items: center;
                // justify-content: center;
                flex-direction: column;
                // text-align: center;
                height: 100%;

                .careers {
                    // height: 50%;
                    align-self: center;
                    justify-self: center;
                }

                p {
                    font-weight: 900;
                    font-size: 25px;

                    span {

                        font-size: 15px;
                    }
                }

                .note {
                    height: 50%;
                }
            }

            .right {
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
                padding: 50px;

                img {
                    width: 40%;
                    // position: absolute;
                    box-sizing: border-box;
                    object-fit: cover;
                }
            }
        }
    }

    .formContainer {
        padding-inline: 10%;

        .heading {
            h2 {
                margin: 0;
            }

            p {
                margin: 0;
                font-size: 13px;
            }
        }

        .form {
            margin-block: 30px;

            p{
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                font-size: 13px;
            }
            .details {
                display: flex;
                gap: 20px;
            }

            .cv {
                label {
                    display: flex;
                    justify-content: space-between;
                    background: #BFBFBF;

                    img{
                        height: 100%;
                        padding-block: 15px;
                    }

                }

                input {
                    display: none;
                }
            }

            button {
                background: #04A8E8;
                height: 50px;
                width: 180px;
                color: #fff;
                border-radius: 15px;
                font-size: 20px;
                margin-top: 30px;
                border: none;
                outline: none;
            }
        }
    }

    .footer{
        display: flex;
        background: #f6f6f6;
    }
}

@keyframes shake {
    0% {
        margin-left: 0rem;
    }

    20% {
        margin-left: 0.5rem;
    }

    40% {
        margin-left: -0.5rem;
    }

    60% {
        margin-left: 0.5rem;
    }

    80% {
        margin-left: -0.5rem;
    }

    100% {
        margin-left: 0rem;
    }

}